import React, { useLayoutEffect, useState } from "react";
import { CDropdown, CDropdownItem, CDropdownMenu, CDropdownToggle } from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { useSelector } from "react-redux";

const TheHeaderDropdown = ({ statusUpdate, handleDelete, displayDetails, id, updateKey, deleteKey, detailsKey }) => {
  const state = useSelector(state => state.users.loggedinUser);
  const { role } = state;
  const [userRole, setUserRole] = useState(role);

  useLayoutEffect(() => {
    setUserRole(role);
  }, [role]);

  return (
    <CDropdown className="c-header-nav-items mx-2" direction="up">
      <CDropdownToggle className="c-header-nav-link" caret={false}>
        <CIcon name="cil-list" />
      </CDropdownToggle>
      <CDropdownMenu className="pb-4 bg-secondary" placement="bottom-end">
        {(userRole === "ADMIN" || userRole === "MANAGER") && updateKey && (
          <CDropdownItem onClick={statusUpdate}>
            <strong id={id}>{updateKey}</strong>
          </CDropdownItem>
        )}
        {(userRole === "ADMIN") && deleteKey && (
          <CDropdownItem onClick={handleDelete}>
            <strong id={id}>{deleteKey}</strong>
          </CDropdownItem>
        )}
        {detailsKey && (
          <CDropdownItem onClick={displayDetails}>
            <strong id={id}>{detailsKey}</strong>
          </CDropdownItem>
        )}
      </CDropdownMenu>
    </CDropdown>
  );
};

export default TheHeaderDropdown;
