import React, { useLayoutEffect } from "react";
import { TheContent, TheFooter, TheHeader } from "./index";
import { decodeToken } from "../redux/actions/index";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { token } from "../redux/config";

const TheLayout = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const userToken = localStorage.getItem("key");

  if (!token || !token.split(" ")[1]) {
    history.push("/login");
  }

  useLayoutEffect(() => {
    const decoded = async () => await dispatch(decodeToken(userToken));
    const valid = decoded();
    valid
      .then(() => {
        history.goForward();
      })
      .catch(() => history.push("/login"));
  }, [userToken, dispatch, history]);

  return (
    <div className="c-app c-default-layout">
      <div className="c-wrapper">
        <TheHeader />
        <div className="c-body">
          <TheContent />
        </div>
        <TheFooter />
      </div>
    </div>
  );
};

export default TheLayout;
